html {
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}
*{
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
:root {
    --font-website: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --color-text: #344854;
}
body {
    padding: 0;
    margin: 0;
    font-family: var(--font-website);
    color: var(--color-text);
    font-size: 1rem;
    position: relative;
}
textarea, input {
    font-family: var(--font-website);
    color: var(--color-text);
}
.-f {
    display: flex;
    flex-wrap: wrap;
}
.-a-c {
    align-items: center;
}
.-j-s-b {
    justify-content: space-between;
}
.-j-c-c {
    justify-content: center;
}
.-h-20 {
    height: 20px !important;
}
.-mb-1 {
    margin-bottom: 1rem;
}
.n {
    padding: 1rem 3rem;
    background-color: #355797;
    color: white;
    &__it {
        img {
            height: 14px;
            margin-right: 10px;
        }
        span {
            font-weight: 500;
        }
        &:hover {
            cursor: pointer;
        }
    }
}
.bn {
    min-height: 200px;
    background: url('../images/bg.jpeg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 16px;
    div {
        flex: 0 0 100%;
        width: 100%;
    }
    &__s-t,
    &__t {
        flex: 0 0 100%;
        width: 100%;
        color: white;
        text-align: center;
        margin: 0;
    }
    &__s-t {
        font-size: 1.25rem;
        line-height: 30px;
    }
    &__t {
        font-size: 2.5rem;
        line-height: 60px;
    }
}
.t {
    text-align: center;
    &__t {
        font-size: 24px;
        font-weight: 500;
        line-height: 29px;
        margin-bottom: 8px;
    }
    &__s-t {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 16px;
    }
}
.f {
    width: 900px;
    max-width: 94%;
    margin: auto;
    box-shadow: 0 2px 4px #00000029,0 2px 10px #0000001f!important;
    padding: 0;
    background-color: #f6f7f8;
    border-top: 2px solid #E7E8E9;
    margin-bottom: 20px;

    &__a {
        img {
            width: 40px;
            flex: 0 0 40px;
            height: 40px;
        }
        b {
            font-weight: 700;
            line-height: 24px;
        }
        p {
            &:nth-of-type(2) {
                line-height: 24px;
                padding-top: 8px;
            }
        }
        &-i {
            width: calc(100% - 56px);
            flex: 0 0 calc(100% - 56px);

            &-l-c {
                span {
                    &:nth-of-type(1) {
                        opacity: 1;
                        color: white;
                        padding: 2px 5px;
                        text-transform: uppercase;
                        background-color: #355797;
                        font-size: 14px;
                        font-weight: 600;
                        border-radius: 3px;
                        margin-right: 5px;
                    }
                    &:nth-of-type(2) {
                        opacity: 0.7;
                        font-size: 14px;
                    }
                }
            }
            &-l {
                line-height: 1.4;
                margin-left: 0;

                span {
                    margin-top: 5px;
                    img {
                        width: 20px;
                        flex: 0 0 20px;
                        height: 20px;
                        margin-right: 5px;
                    }
                    span {
                        margin-top: 0;
                        width: calc(100% - 25px);
                        flex: 0 0 calc(100% - 25px);
                    }
                }
            }
        }
        &-0 {
            padding: 30px;
            background-color: white;
        }
        &-1 {
            padding: 30px;
            border-bottom: 1px solid #e7e8e9;
        }
        &-2 {
            padding: 30px 30px 0;
        }
        &-3 {
            padding: 0 30px 30px;
        }
    }
    &__f {
        margin-left: 56px;
        &-g {
            padding-top: 1.5rem;
            position: relative;
            label {
                font-size: 13px;
                font-weight: 500;
                color: #959699;
                line-height: 19.5px;
            }
        }
        &-i {
            display: block;
            width: 100%;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5;
            background-clip: padding-box;
            padding: 0.375rem 0.75rem;
            background-color: white;
            border: 1px solid #bdbdbd;
            border-radius: .375rem;
            transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
            outline: none;
            &:focus {
                box-shadow: rgba(13, 110, 253, 0.25) 0px 0px 0px 0.25rem;
                border-color: rgb(134, 183, 254);
            }
            &-o-p {
                position: absolute;
                display: block;
                line-height: 0;
                right: 10px;
                top: 54px;
                z-index: 2;
                img {
                    width: 20px;
                }
            }
        }
        &-c {
            padding-top: 1rem;
            margin-bottom: 0.125rem;
            label {
                font-size: 13px;
                font-weight: 500;
                color: #959699;
                line-height: 19.5px;
            }
            &b {
                width: 1em;
                height: 1em;
                margin: 0.25em 5px 0 0;
                vertical-align: top;
                background-color: white;
                border-radius: 0.25em;
                border: 1px solid #dee2e6;
            }
        }
    }
}
.-mt-4 {
    margin-top: 1.5rem
}
.b {
    padding: 6px 12px;
    font-weight: 500;
    line-height: 24px;
    color: white;
    background-color: #355797;
    border: none;
    border-radius: 0;
    font-size: 1rem;
    cursor: pointer;
    &:disabled {
        pointer-events: none;
        opacity: 0.6;
    }
    &-c {
        background-color: gray !important;
    }
}
.ctn {
    width: 1200px;
    max-width: 100%;
    padding: 0 15px;
    padding-top: 48px;
    margin: auto;
    &__l {
        flex: 0 0 260px;
        width: 260px;
        &-n {
            padding: 0;
            color: #cbd2d9;
            font-size: 12px;
            line-height: 16px;
        }
        &-ic {
            margin-top: 12px;
            a {
                display: inline-block;
                margin-right: 7px;
                img {
                    width: 24px;
                }
            }
        }
    }
    &__ll {
        flex: 0 0 calc(100% - 300px);
        width: calc(100% - 300px);
        height: min-content;
        &-r {
            margin-bottom: 16px;
            a {
                color: #cbd2d9 !important;
                font-size: 12px;
                line-height: 16px;
                flex: 1;
            }
        }
    }
}
a {
    text-decoration: none;
}
footer {
    background-color: #000000;
    padding-bottom: 80px;
    padding-top: 30px;
}
.ctn-li {
    width: 1170px;
    max-width: 100%;
    margin: auto;
    height: 1px;
    background-color: #465a69;
}
.i-ar {
    border-color: inherit;
    border-style: solid;
    border-width: 1px;
    height: 7px;
    margin: 0 0 1px 3px;
    transform: rotate(45deg);
    width: 7px;
    border-bottom: none;
    border-left: none;
    display: inline-block;
}
.md {
    display: flex;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    &__ct {
        position: relative;
        background-color: white;
        padding: 0;
        border: 1px solid #888;
        width: 500px;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
        -webkit-animation-name: animatetop;
        -webkit-animation-duration: 0.4s;
        animation-name: animatetop;
        animation-duration: 0.4s;
        border-radius: .5rem;
        max-width: 94%;
        &-cl {
            color: var(--color-text);
            font-size: 34px;
            cursor: pointer;
            font-weight: 300;
            line-height: 0;
        }
        &-h {
            padding: 16px;
            border-bottom: 1px solid #dee2e6;
            h2 {
                font-weight: 700;
            }
        }
        &-b {
            padding: 16px;
        }
        &-ft {
            padding: 16px;
            text-align: right;
            border-top: 1px solid #dee2e6;
        }
        &-al {
            color: red;
            padding-top: 0.5rem;
        }
    }
    &__gd {
        z-index: 2;
        b {
            font-weight: 600;
        }
        .md__ct-te {
            margin-bottom: 15px;
            line-height: 1.4;
            font-size: 15px;
        }
    }
}
.md__v-f {
    background-color: white;
    .md__ct {
        width: 750px;
        border: none;
        border-radius: 0;
        box-shadow: 0 2px 4px #00000029,0 2px 10px #0000001f!important;
    }
}
.ma {
    min-height: 100vh;
    background: white;
    position: relative;
    z-index: 2;
    &__m {
        background-color: #fff;
        padding: 30px;
        border-radius: 4px;
        font-size: 14px;
        width: 675px;
        max-width: 94%;
        img {
            width: 135px;
            flex: 0 0 135px;
            height: 135px;
        }
        &-i {
            width: calc(100% - 145px);
            flex: 0 0 calc(100% - 145px);
            &-t {
                font-weight: 600;
                margin-bottom: 4px;
            }
            &-s-t {
                margin-bottom: 16px;
                line-height: 21px;
            }
        }
        &-p {
            background-color: #e9ecef;
            border-radius: 6px;
            height: 16px;
            position: relative;
            &-r {
                position: absolute;
                z-index: 1;
                top: 0;
                left: 0;
                height: 100%;
                width: 0;
                background-color: #0d6efd;
                border-radius: 6px;
            }
        }
    }
}
.ss {
    min-height: 100vh;
    background: white;
    position: relative;
    z-index: 2;
    &__m {
        background-color: #fff;
        padding: 0;
        border-radius: 4px;
        font-size: 14px;
        width: 600px;
        max-width: 94%;
        &-i {
            text-align: center;
            img {
                width: 150px;
                height: 84px;
            }
        }
        &-t {
            font-size: 32px;
            font-weight: 500;
            line-height: 38.4px;
        }
        &-te {
            margin-top: 8px;
            font-size: 18px;
            line-height: 1.4;
            text-align: center;
        }
    }
}
.m {
    height: 100vh;
    position: fixed;
    background-color: white;
    width: 100%;
    z-index: 10;
    left: 0;
    top: 0;
    overflow: auto;
}
.m__w {
    width: 600px;
    max-width: 96%;
    text-align: center;
    position: relative;
    z-index: 2;
}
.m__w--b {
    width: 100%;
    margin-bottom: 10px;
}
.m__w a {
    width: 100%;
    background: rgb(37, 116, 234);
    color: rgb(255, 255, 255) !important;
    display: block;
    text-decoration: none;
    border-radius: 6px;
    padding: 6px 12px;
    line-height: 24px;
}
.bl {
    position: absolute;
    bottom: 5px;
    z-index: 1;
    text-decoration: none;
    color: var(--color-text);
    font-weight: 500;
    left: 50%;
    transform: translateX(-50%);
}
.b2 {
    background-color: rgb(48, 132, 244) !important;
    width: 100%;
    padding: 10px 12px;
    border-radius: 4px;
    font-weight: 600;
}
.b3 {
    background-color: #1777ff !important;
    width: 100%;
    padding: 10px 12px;
    border-radius: 4px;
    font-weight: 600;
}
.ta .ss__m {
    border: none;
    width: 600px;
}
.ta .ss__m-i img {
    height: auto !important;
}
.ta .ss__m-t {
    font-size: 22px;
    margin-bottom: 15px;
    line-height: 1.4;
}
.ta .ss__m-te {
    font-size: 18px;
    margin-bottom: 15px;
}
.ta .b {
    width: 300px;
    display: block;
    text-align: center;
    margin: auto;
    max-width: 100%;
}
@-webkit-keyframes animatetop {
    from {top:-300px; opacity:0} 
    to {top:0; opacity:1}
}
@keyframes animatetop {
    from {top:-300px; opacity:0}
    to {top:0; opacity:1}
}
@media only screen and (max-width: 768px) {
    .ctn-li {
        display: none;
    }
    .n {
        padding: 1rem 24px;
    }
    .t {
        padding: 0 24px;
    }
    .ctn {
        padding: 0 24px;
        padding-top: 48px;
    }
    .ctn__l {
        flex: 0 0 100%;
        width: 100%;
        padding-bottom: 24px;
        border-bottom: 1px solid #465a69;
    }
    .ctn__ll {
        flex: 0 0 100%;
        width: 100%;
    }
    .ctn__ll-r {
        border-bottom: 1px solid #344854;
        margin-bottom: 24px;
        padding: 24px 0 8px;
        a {
            flex: 0 0 110px;
            width: 110px;
            margin-bottom: 16px;
        }
    }
    .ctn__ll-r2 {
        padding-top: 0;
        border-bottom: none;
    }
}
@media only screen and (max-width: 480px) {
    .n__it {
        img {
            margin-right: 0;
        }
    }
    .bn__t {
        font-size: 30px;
        line-height: 1.4;
    }
    .bn__s-t {
        font-size: 16px;
        line-height: 24px;
    }
    .ma__m-i {
        flex: 0 0 100%;
        width: 100%;
        margin-top: 10px;   
    }
    .ma__m img {
        margin: auto;
    }
}